@charset "UTF-8";

$brand-primaria: #003D4D;
$brand-secundaria: #0F599C;
$brand-terciaria: #168B87;
$brand-light: #E6E6E6;
$brand-dark: #333333;

$body-padding-top: 56px;

@import "modal";

html {
    scroll-behavior: smooth;
    scroll-padding-top: $body-padding-top;
}

body {
    padding: $body-padding-top 0 0 0;
    font-family: 'Open Sans', sans-serif;
}

h1, h2, h3, h4, h5 {
    font-family: 'Roboto Condensed', sans-serif;
    font-weight: 700;
}

section {
    padding: 3rem 0;
    min-height: calc(100vh - #{$body-padding-top});

    h2 {
        font-size: 2.5rem;
    }
}

.button {
    display: inline-block;
    padding: .25rem .75rem;
    border-radius: 5px;
    border: none;
    font-family: 'Open Sans', sans-serif;
    text-decoration: none;
    color: inherit;
}

.button.button-primary {
    background: $brand-primaria;
    color: $brand-light;
}

.button.button-primary:hover {
    background: #00566b;
    color: #fff;
}

.button.button-secondary {
    background: $brand-secundaria;
    color: $brand-light;
}

.button.button-secondary:hover {
    background: #1164b1;
    color: #fff;
}

.button.button-light {
    background: $brand-light;
    color: $brand-dark;
}

.button.button-light:hover {
    background: #fff;
    color: #444444;
}

form label {
    font-size: .9rem;
}

nav.navbar {
    background: $brand-primaria;
}

#apresentacao {
    color: $brand-light;
    height: calc(100vh - $body-padding-top);
    background: $brand-primaria;
    background-image: url("assets/apresentacao-bg.jpg");
    background-repeat: no-repeat;
    background-position: left bottom; 
    background-size: cover;
}

#quemsomos {
    position: relative;
    background: $brand-terciaria;
    background-image: url("assets/quem-somos-bg.jpg");
    background-attachment: fixed;
    background-position: left bottom;
    background-size: cover;

    color: $brand-light;

    .divider-turquoize {
        display: block;
        width: 100%;
        height: auto;
        position: absolute;
        bottom: -2px; left: 0;
    }
}

#servicos {
    position: relative;
    background: $brand-secundaria;
    padding: 1.5rem 0 3rem;

    .divider-grey {
        position: absolute;
        width: 100%;
        height: auto;
        left: 0;
        bottom: -2px;
    }

    p{
        color: $brand-light
    }

    h2, h3 {
        color: #fff;
    }
}

#alerta {
    background: $brand-light;
}

#oferta {
    position: relative;
    background: $brand-primaria;
    background-image: url(assets/halftone.png);
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: left center;
    background-size: cover;
    background-blend-mode:color-dodge;
    color: $brand-light;

    .divider-grey {
        position: absolute;
        width: 100%;
        height: auto;
        left: 0;
        bottom: -2px;
    }

    .valor-oferta {
        position: relative;
        border: 2px solid $brand-light;
        transform: rotateZ(-2deg);
        display: inline-block;
        background: $brand-terciaria;
        padding: .75rem 1.2rem;
        border-radius: 5px;
        box-shadow: 5px 5px 16px rgba(0,0,0,0.3);
        text-align: center;
        color: $brand-light;

        p { margin: 0; }

        .legenda {
            font-size: 1.25rem;
            line-height: 1.25rem;
            margin-bottom: .25rem;
        }

        .valor {
            font-family: 'Roboto Condensed', sans-serif;
            font-weight: 700;
            font-size: clamp(2rem, 8vw, 4rem);
            line-height: 3.75rem;
        }
    }
}

#contato {
    background: $brand-light;
    padding: 3rem 0;
}

.contato-list-links {
    padding: 0;
    list-style: none;
    margin-bottom: 2rem;

    li a {
        display: inline-block;
        padding: .25rem 0;
        color: inherit;
        text-decoration: none;
    }

    li a:hover {
        color: $brand-terciaria;
    }
}

#mapa {
    padding: 0;
    min-height: 75vh;
}

.mapa-google {
    width: 100%;
    height: 75vh;
    padding: 0; margin: 0;
}

footer {
    background: $brand-primaria;
    color: $brand-light;
    font-size: .9rem;
    padding: 1rem 0;
}


// Lazzy effect
.delay-1 {
    transition-delay: 1s;
}

.delay-2 {
    transition-delay: 1.5s;
}

.delay-3 {
    transition-delay: 2s;
}

.from-top {
    transform: translateY(-150px);
    opacity: 0;
    transition-property: transform opacity;
    transition-duration: .5s;
}

.from-top.play {
    transform: translateY(0);
    opacity: 1;
}

.from-bottom {
    transform: translateY(150px);
    opacity: 0;
    transition-property: transform opacity;
    transition-duration: .5s;
}

.from-bottom.play {
    transform: translateY(0);
    opacity: 1;
}

.from-left {
    transform: translateX(-150px);
    opacity: 0;
    transition-property: transform opacity;
    transition-duration: .5s;
}

.from-left.play {
    transform: translateX(0);
    opacity: 1;
}

.from-right {
    transform: translateX(150px);
    opacity: 0;
    transition-property: transform opacity;
    transition-duration: .5s;
}

.from-right.play {
    transform: translateX(0);
    opacity: 1;
}

// Slider
.slider {
    display: flex;
    align-items: stretch;
    position: relative;
    // background: #f9f9f9;
    min-width: 100%;
    min-height: 400px;
}

.slider-nav {
    position: absolute;
    cursor: pointer;
}

.slider-nav img {
    width: 1.5rem;
    height: auto;
    opacity: .5;
}

.slider-nav img:hover {
    opacity: .7;
    transform: scale(1.1);
}

.slider-prev {
    left: 1rem;
    top: 50%;
    transform: translateY(-50%);
}

.slider-next {
    right: 1rem;
    top: 50%;
    transform: translateY(-50%);
}

.slider-dots {
    padding: 1rem 0;
    position: absolute;
    width: 100%;
    bottom: 0;
    left: 0;
    display: flex;
    justify-content: center;
    z-index: 1029;
}

.slider-dots__item {
    position: relative;
    cursor: pointer;
    border-radius: 50%;
    margin: 5px;
    width: 8px;
    height: 8px;
    border: 1px solid #e9e9e9;
    opacity: .7;
}

.slider-dots__item::before {
    content: '';
    position: absolute;
    /* background: red; */
    display: block;
    width: 16px;
    height: 16px;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}

.slider-dots__item:hover {
    opacity: 1;
    background: rgba(255, 255, 255, .75);
}

.slider-dots__item.active {
    opacity: 1;
    transform: scale(1.2);
}

.slider-wraper {
    display: flex;
    flex-wrap: nowrap;
    overflow: hidden;
    width: 100%;
    min-height: 400px;
    scroll-behavior: smooth;
    scroll-snap-type: x mandatory;
}

.slider-item {
    padding: 0 3rem;
    min-height: 400px;
    flex: 0 0 100%;

    display: flex;
    justify-content: center;
    align-items: stretch;

    scroll-snap-align: start;
}

.slider-item__column {
    text-align: center;
    flex-basis: 50%;
}


// Whatsapp button
.zap {
    position: fixed;
    bottom: 1rem;
    right: 1rem;

    .zap-button {
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
        background: #016b01;
        color: #f9f9f9;
        border-radius: 50%;
        border: 1px solid #f9f9f9;
        padding: .75rem;
        font-size: 2rem;
        text-decoration: none;
        box-shadow: 0 0 10px rgba(0,0,0,0.3);
    }
    .zap-button:hover {
        background: #008000;
        color: #fff;
    }
    .zap-pulse {
        width: 100%; height: 100%;
        position: absolute;
        right: 0; bottom: 0;
        background: #fff;
        border-radius: 50%;
        z-index: -2;
        animation-name: zap-pulse;
        animation-duration: 2s;
        animation-iteration-count: infinite;
    }
}

@keyframes zap-pulse {
    0% {transform: scale(1); opacity: .5;}
    100% {transform: scale(1.3); opacity: 0;}
}